export const iconClass = (key) => {
    switch(key){
        case 'recipe': return "fas fa-code";
        case 'photo': return "fas fa-camera";
        case 'reading': return "fas fa-book";
        case 'novel': return "fas fa-feather";
        case 'home': return "fas fa-home";
        case 'enter': return "fas fa-arrow-circle-right fa-lg";
        case 'list': return "fas fa-angle-right";
        default: return "fas fa-angry";
    }
}

